import React, { useState, useEffect } from 'react';
import './bookme.css';
import { createClient } from '@supabase/supabase-js';

// Create a single supabase client for interacting with your database
const supabase = createClient(
  'https://xbgzzblzosbnmpmsyzsi.supabase.co',
  'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6InhiZ3p6Ymx6b3Nibm1wbXN5enNpIiwicm9sZSI6ImFub24iLCJpYXQiOjE3MjM3OTU3MTcsImV4cCI6MjAzOTM3MTcxN30.osECWypP17FMvOi_VBUZWLhH_2dCOP6JDrHLGgeiWMU'
);

const BookMe = () => {
  const [showModal, setShowModal] = useState(false);
  const [widgets, setWidgets] = useState([]);
  const [appointments, setAppointments] = useState([]);
  const [widgetName, setWidgetName] = useState('');
  const [widgetStart, setWidgetStart] = useState(0);
  const [widgetEnd, setWidgetEnd] = useState(0);
  const [selectedDay, setSelectedDay] = useState(null);

  // Carica gli appuntamenti dal database quando il componente viene montato
  useEffect(() => {
    const loadAppointments = async () => {
      let { data: Appointments, error } = await supabase
        .from('Appointments')
        .select("*")
        .gte('date', new Date().toISOString().split('T')[0]); // Filtra per date future o odierne

      if (error) {
        console.error("Errore nel caricamento degli appuntamenti:", error);
      } else {
        setAppointments(Appointments);
      }
    };

    loadAppointments();
  }, []);

  // Funzione per ottenere i prossimi 7 giorni
  const getNextDays = () => {
    const days = [];
    const today = new Date();
    for (let i = 0; i < 7; i++) {
      const date = new Date(today);
      date.setDate(today.getDate() + i);
      days.push(date);
    }
    return days;
  };

  const createCalendar = () => {
    const days = getNextDays();
    const currentDate = new Date();

    return days.map(day => (
      <div className="day-column" data-day={day.toDateString()} key={day.toDateString()}>
        <div className="day-header" onClick={() => openModal(day)}>
          {day.toLocaleDateString('it-IT', { weekday: 'long', day: 'numeric', month: 'long' })}
        </div>
        <div className="pills-container">
          {[...Array(24)].map((_, hour) => {
            const isBooked = appointments.some(appointment => {
              return (
                new Date(appointment.date).toDateString() === day.toDateString() &&
                hour >= appointment.start_hour &&
                hour <= appointment.end_hour
              );
            });

            return (
              <div
                className={`hour-pill ${isBooked ? 'locked' : 'unlocked'}`}
                key={hour}
                onClick={(e) => toggleLock(e)}
              >
                {`${hour}:00`}
              </div>
            );
          })}
        </div>
      </div>
    ));
  };

  // Funzione per aprire la modale
  const openModal = (day) => {
    setSelectedDay(day);
    setShowModal(true);
  };

  // Funzione per chiudere la modale
  const closeModal = () => {
    setShowModal(false);
  };

  // Funzione per bloccare/sbloccare l'ora
  const toggleLock = (e) => {
    const pill = e.target;
    if (pill.classList.contains('locked')) {
      pill.classList.remove('locked');
      pill.classList.add('unlocked');
    } else {
      pill.classList.remove('unlocked');
      pill.classList.add('locked');
    }
  };

  // Funzione per creare un widget e aggiungere un nuovo appuntamento nel database
  const createWidget = async () => {
    const newWidget = {
      name: widgetName,
      start: widgetStart,
      end: widgetEnd,
    };
    setWidgets([...widgets, newWidget]);
    setWidgetName('');
    setWidgetStart(0);
    setWidgetEnd(0);

    // Inserisci il nuovo appuntamento nel database
    if (selectedDay) {
      const { data, error } = await supabase
        .from('Appointments')
        .insert([
          {
            date: selectedDay.toISOString().split('T')[0],
            start_hour: widgetStart,
            end_hour: widgetEnd
          }
        ]);

      if (error) {
        console.error("Errore nell'inserimento dell'appuntamento:", error);
      } else {
        setAppointments([...appointments, ...data]);
      }
    }
  };

  const applyWidget = (widget) => {
    if (selectedDay) {
      // Trova la colonna del giorno selezionato usando un attributo data o un ID
      const dayColumn = document.querySelector(`[data-day="${selectedDay.toDateString()}"] .pills-container`);

      if (dayColumn) {
        const pills = dayColumn.querySelectorAll('.hour-pill');
        for (let i = widget.start; i <= widget.end; i++) {
          pills[i].classList.add('locked');
          pills[i].classList.remove('unlocked');
        }
      } else {
        console.error("Colonna del giorno non trovata per la data:", selectedDay.toDateString());
      }
    }
    closeModal();
  };

  return (
    <div id="calendar-body">
      <div id="calendar">
        {createCalendar()}
      </div>

      {showModal && (
        <div className="modal">
          <div className="modal-content">
            <span className="close" onClick={closeModal}>&times;</span>
            <h2>Crea un Widget</h2>
            <input
              type="text"
              placeholder="Nome del Widget"
              value={widgetName}
              onChange={(e) => setWidgetName(e.target.value)}
            />
            <label>
              Ora di inizio:
              <input
                type="number"
                min="0"
                max="23"
                value={widgetStart}
                onChange={(e) => setWidgetStart(Number(e.target.value))}
              />
            </label>
            <label>
              Ora di fine:
              <input
                type="number"
                min="0"
                max="23"
                value={widgetEnd}
                onChange={(e) => setWidgetEnd(Number(e.target.value))}
              />
            </label>
            <button onClick={createWidget}>Crea Widget</button>

            <h2>Applica un Widget</h2>
            {widgets.length > 0 ? (
              widgets.map((widget, index) => (
                <div key={index}>
                  <button onClick={() => applyWidget(widget)}>{widget.name}</button>
                </div>
              ))
            ) : (
              <p>Non ci sono widgets disponibili.</p>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default BookMe;
